import { useRef } from "react";
import {
  Box,
  Button,
  Drawer,
  DrawerBody,
  DrawerCloseButton,
  DrawerContent,
  DrawerOverlay,
  Flex,
  IconButton,
  useDisclosure,
  VStack,
} from "@chakra-ui/react";

import { Link } from "react-scroll";
import styles from "../Styles/Navbar.module.css";
import { DownloadIcon, HamburgerIcon } from "@chakra-ui/icons";

function Navbar() {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = useRef();

  return (
    <>
      <div className={styles.main_nav}>
        <div className={styles.logo_nav}>
          <h2 className={styles.logo}>SD</h2>
         
          <div className={styles.navbar}>
            <Flex className={styles.nav2}>
              <Box className={styles.box}>
                <Link
                  cursor="pointer"
                  to="home"
                  smooth={true}
                  duration={900}
                  spy={true}
                  offset={-100}
                >
                  HOME
                </Link>
              </Box>
              <Box className={styles.box}>
                <Link
                  to="about"
                  smooth={true}
                  duration={900}
                  spy={true}
                  offset={-40}
                >
                  ABOUT
                </Link>
              </Box>
              <Box className={styles.box}>
                <Link
                  to="skills"
                  smooth={true}
                  duration={900}
                  spy={true}
                  offset={-40}
                >
                  SKILLS
                </Link>
              </Box>
              <Box className={styles.box}>
                <Link
                  to="projects"
                  smooth={true}
                  duration={900}
                  spy={true}
                  offset={-30}
                >
                  PROJECTS
                </Link>
              </Box>
              <Box className={styles.box}>
                <Link
                  to="contact"
                  smooth={true}
                  duration={900}
                  spy={true}
                  offset={-30}
                >
                  CONTACT
                </Link>
              </Box>
              <a href={"Resume/Shibangi_Das_Resume.pdf"} download>
                <Button
                  onClick={() =>
                    window.open(
                      "https://drive.google.com/file/d/1RrkUgSzYMVc3eC13_mecl-y4IwTc6GXS/view?usp=share_link"
                    )
                  }
                  mt={5}
                  mb={5}
                  className={styles.resume}
                >
                  RESUME
                  <DownloadIcon ml="9px" className={styles.dwnIcn} />
                </Button>
              </a>
            </Flex>
          </div>

          <IconButton
            size={"lg"}
            mr={35}
            onClick={onOpen}
            border={"none"}
            icon={<HamburgerIcon className={styles.hamburger} />}
            className={styles.iconBtn}
          />
        </div>
      </div>
    </>
  );
}

export default Navbar;




